body {
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  line-height: 1.2;
  color: #000;
  font-size: 400px;
}

* {
  box-sizing: border-box;
}

a {

  &:hover {
    text-decoration: none;
  }
}

button {
  border: 0;
  &:hover,
  &:focus {
    outline: none;
  }
}

.container {
  width: 664px;
  margin-left: auto;
  margin-right: auto;
}

.clearfix {
  &:before,
  &:after {
    content: '';
    display: table;
    clear: both;
  }
}

.col-50 {
  float: left;
  width: 50%;
  padding-left: 5px;
  padding-right: 5px;
}

.table {
  display: table;
  width: 100%;
}

.table-cell {
  display: table-cell;
  vertical-align: top;
  padding: 0 5px;
}

.copy {
  text-align: center;
  margin-top: 40px;
  margin-bottom: 20px;
  font-size: 14px;
}