.form {
  &__title {
    color: #333;
    font-size: 30px;
    text-align: center;
    margin-bottom: 20px;
  }

  &__box {
    font-size: 14px;
    margin-top: 25px;
  }

  &-label__box {
    display: inline-block;
    vertical-align: top;
    padding-left: 5px;
    padding-right: 5px;
  }

  &__row {
    margin: 0 -5px 10px;
  }
}

.form-group {
  margin-bottom: 15px;
}

.form-control {
  display: block;
  width: 100%;
  height: 34px;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.42857143;
  color: #555;
  background-color: #fff;
  background-image: none;
  border: 1px solid #ccc;
  border-radius: 4px;
  -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
  -webkit-transition: border-color ease-in-out .15s,-webkit-box-shadow ease-in-out .15s;
  -o-transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;
  transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;

  &:focus,
  &:active {
    outline: none;
  }
}

textarea.form-control {
  height: 120px;
  resize: none;
}

.label__title {
  margin-bottom: 5px;
  font-size: 14px;

  &.border {
    border-bottom: 1px solid #ccc;
    padding-bottom: 5px;
  }
}

.form-btn {
  text-align: center;
  margin-top: 25px;
}

.send-btn {
  display: inline-block;
  height: 40px;
  line-height: 38px;
  text-align: center;
  padding: 0 35px;
  background: #ccc;
  cursor: pointer;
  border: 1px solid #333;
  border-radius: 5px;
  font-weight: 700;

  &:hover {
    opacity: .8;
  }
}


.parsley-errors-list {
  color: red;
  font-size: 12px;
}
.parsley-error {
  border-color: red;
}